import React, {PureComponent} from 'react';
import {connect} from 'react-redux';
import {Button, Table} from 'antd';
import {Link} from "react-router-dom";
import {actionCreators} from './store';

class Novel extends PureComponent {
  render() {
    const {list} = this.props;
    const columns = [
      {
        key: 'title',
        dataIndex: 'title',
        title: '小说名称',
        align: 'center'
      }, {
        key: 'page_title',
        dataIndex: 'page_title',
        title: '首章标题',
        align: 'center'
      }, {
        key: 'image_url',
        title: 'banner图',
        align: 'center',
        render(item, record, index) {
          const imgStyle = {
            width: '100px',
            verticalAlign: 'middle'
          };
          return (
            <img src={record.image_url} style={imgStyle} alt=""/>
          )
        }
      }, {
        key: 'action',
        title: '操作',
        align: 'center',
        render(item, record, index) {
          return (
            <div>
              <Button style={{marginRight: '10px'}} size='small' type='primary'>
                <Link to={'/build/novel/modify?record=' + JSON.stringify(record)}>修改</Link>
              </Button>
              <Button size='small'>
                <Link to={'/build/novel/config?id=' + record.id + '&title=' + record.title}>配置</Link>
              </Button>
            </div>
          )
        }
      }];
    return <Table rowKey={record => record._id} columns={columns} dataSource={list} bordered/>
  }

  componentDidMount() {
    this.props.getNovelList()
  }
}

const mapState = (state) => ({
  list: state.get('novel').list,
  columns: state.get('novel').columns
});

const mapDispatch = (dispatch) => {
  return {
    getNovelList() {
      dispatch(actionCreators.getNovelList())
    }
  }
};

export default connect(mapState, mapDispatch)(Novel);