import React, { PureComponent } from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter, Route, Redirect } from 'react-router-dom';
import Header from './common/header';
import Slider from './common/slider';
import Home from './pages/home';
// import Media from './pages/media';
import MediaCreate from './pages/media/create';
import MediaShow from './pages/media/show';
import AdvertiseCreate from './pages/advertise/create';
import AdvertiseShow from './pages/advertise/show';
import Novel from './pages/novel';
import Modify from './pages/novel/modify';
import Config from './pages/novel/config';
import CreateRule from "./pages/novel/config/create";
import store from './store';

class App extends PureComponent {
  render() {
    return (
      <Provider store={store}>
        <BrowserRouter>
          <Header />
          <Slider />
          <div style={{margin: '65px 0 0 241px'}}>
            <Route path='/' exact render={() => <Redirect to='/build' />} />
            <Route path='/build' exact component={Home} />
            {/*<Route path='/build' exact render={() => <Redirect to='/build/novel' />} />*/}
            <div style={{padding: '30px'}}>
              <Route path='/build/media/create' component={MediaCreate}></Route>
              <Route path='/build/media/show' component={MediaShow}></Route>
              <Route path='/build/advertise/create' component={AdvertiseCreate}></Route>
              <Route path='/build/advertise/show' component={AdvertiseShow}></Route>
              <Route path='/build/novel' exact component={Novel} />
              <Route path='/build/novel/modify' match component={Modify} />
              <Route path='/build/novel/config' exact component={Config} />
              <Route path='/build/novel/config/create' component={CreateRule}/>
            </div>
          </div>
        </BrowserRouter>
      </Provider>
    );
  }
}

export default App;
