import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import {
  HeaderWrapper,
  Logo,
  Addition,
  Span
} from './style';

class Header extends PureComponent {

  render() {
    return (
      <HeaderWrapper>
        <Link to='/novel'>
          <Logo>小说管理后台</Logo>
        </Link>
        <Addition>
          <Span>消息</Span>
          <Span>退出</Span>
        </Addition>
      </HeaderWrapper>
    )
  }
}

export default Header;