import React, { PureComponent } from 'react';
import { Table, Button } from 'antd';

class AdvertiseShow extends PureComponent {
  render() {
    const columns = [{
      key: 'ad_name',
      title: '⼴告位名称',
      dataIndex: 'ad_name',
      align: 'center'
    }, {
      key: 'media',
      title: '所属媒体',
      dataIndex: 'media',
      align: 'center'
    }, {
      key: 'ad_type',
      title: '广告位形式',
      dataIndex: 'ad_type',
      align: 'center'
    }, {
      key: 'action',
      title: '操作',
      align: 'center',
      render(item, record, index) {
        return <Button type='primary' size='small'>复制链接</Button>
      }
    }];

    const list = [{
      ad_name: '小说广告',
      media: '豆盟',
      ad_type: '图标',
      key: '1'
    }];
    return <Table bordered columns={columns} dataSource={list} />
  }
}

export default AdvertiseShow;