import React, { PureComponent } from 'react';
import { Form, Select, Input, Radio, Button } from 'antd';
const Option = Select.Option;

class AdvertiseCreate extends PureComponent {
  render() {
    const FormLabelCol = {
      labelCol: {
        sm: {span: 3},
      },
      wrapperCol: {
        sm: {span: 8},
      },
      colon: false
    }
    return (
      <Form {...FormLabelCol}>
        <Form.Item label='媒体选择'>
          <Select defaultValue="lucy" style={{ width: 120 }}>
            <Option value="jack">Jack</Option>
            <Option value="lucy">Lucy</Option>
            <Option value="disabled" disabled>Disabled</Option>
            <Option value="Yiminghe">yiminghe</Option>
          </Select>
        </Form.Item>
        <Form.Item label='广告位形式'>
          <Radio.Group>
            <Radio value='icon'>图标</Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item label='广告位名称'>
          <Input placeholder='限20个字'/>
        </Form.Item>
        <Form.Item label=' '>
          <Button type='primary'>立即创建</Button>
        </Form.Item>
      </Form>
    )
  }
}

export default AdvertiseCreate;