import * as constants from './constants';
import axios from "axios";

export const getNovelList = () => {
  return (dispatch) => {
    axios.get('http://h.tyuss.com/book/all').then(res => {
      let list = res.data.data;
      const action = {
        type: constants.GET_NOVEL_LIST,
        list
      };
      dispatch(action)
    })
  }
};