import React, { PureComponent } from 'react';
import { Table } from 'antd';

class MediaShow extends PureComponent {
  render() {
    const columns = [{
      key: 'type',
      title: '媒体类型',
      dataIndex: 'type',
      align: 'center'
    }, {
      key: 'name',
      title: '媒体名称',
      dataIndex: 'name',
      align: 'center'
    }, {
      key: 'api_key',
      title: 'API KEY',
      dataIndex: 'api_key',
      align: 'center'
    }];

    const list = [{
      type: 'IOS',
      name: '微信订阅号',
      api_key: '72acadabc56eaee10d593ce6f0629508',
      key: '1'
    }];
    return <Table bordered columns={columns} dataSource={list} />
  }
}

export default MediaShow;