import React, {PureComponent} from 'react';
import { Card, Table, Button } from 'antd';

import './index.css';

class Home extends PureComponent {

  render() {
    const CardHeadStyle = {
      'color': '#00a0fe',
      'fontSize': '24px',
      'border': 0
    };
    const columns = [{
      key: 'date',
      dataIndex: 'date',
      title: '日期',
      align: 'center'
    }, {
      key: 'rechargeIncome',
      dataIndex: 'rechargeIncome',
      title: '小说充值收入',
      align: 'center'
    }, {
      key: 'adIncome',
      dataIndex: 'adIncome',
      title: '广告收入',
      align: 'center'
    }, {
      key: 'generalIncome',
      dataIndex: 'generalIncome',
      title: '总收入',
      align: 'center'
    }, {
      key: 'proportion',
      dataIndex: 'proportion',
      title: '分成比例',
      align: 'center'
    }, {
      key: 'proportionIncome',
      dataIndex: 'proportionIncome',
      title: '分成收⼊',
      align: 'center'
    }, {
      key: 'action',
      title: '操作',
      align: 'center',
      render(item, record, index) {
        return <Button type='primary' size='small'>编辑</Button>
      }
    }];
    const list = [{
      date: '2019-04-18',
      rechargeIncome: '321.09',
      adIncome: '321.09',
      generalIncome: '321.09',
      proportion: '90%',
      proportionIncome: '876.54',
      key: '1'
    }];
    return (
      <div className='home'>
        <Card className='card_wrapper' title='账户收入' headStyle={CardHeadStyle} bordered={false}>
          <Card className='card_item'>
            <p className='account_title'>账户余额（元）</p>
            <span className='account_num'>4567.89</span>
          </Card>
          <Card className='card_item'>
            <p className='account_title'>累计收⼊（元）</p>
            <span className='account_num'>4567.89</span>
          </Card>
        </Card>
        <Table className='table_list' bordered columns={columns} dataSource={list} />
      </div>
    )
  }
}

export default Home;