import React, {PureComponent} from 'react';
import {Form, Input, Radio, Button} from 'antd';

const {TextArea} = Input;

class MediaCreate extends PureComponent {
  render() {
    const FormLabelCol = {
      labelCol: {
        sm: {span: 2},
      },
      wrapperCol: {
        sm: {span: 8},
      },
      colon: false
    };
    return (
      <Form {...FormLabelCol} >
        <Form.Item label='媒体名称'>
          <Input/>
        </Form.Item>
        <Form.Item label='媒体类型'>
          <Radio.Group>
            <Radio value="andriod">Ardroid</Radio>
            <Radio value="ios">IOS</Radio>
            <Radio value="wechat">微信</Radio>
            <Radio value="others">其他</Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item label='应用简介'>
          <TextArea rows={4}/>
        </Form.Item>
        <Form.Item label=' '>
          <Button type='primary'>立即创建</Button>
        </Form.Item>
      </Form>
    )
  }
}

export default MediaCreate;