import React, { PureComponent, Fragment } from 'react';
import { Link } from 'react-router-dom';
import {Table, Button, message, Popconfirm} from 'antd';
import axios from 'axios';

class Config extends PureComponent {
  constructor(props) {
    super(props);
    let record = decodeURI(props.location.search);
    let id = record.split('=')[1].split('&')[0];
    let title = record.split('=')[2]
    this.state = {
      rules: [],
      book: {id, title},
      chapterCount: null
    };
    this.handleDeleteItem = this.handleDeleteItem.bind(this);
  };
  render() {
    let that =this;
    const { rules } = this.state;
    const columns = [
      {
        key: 'price',
        dataIndex: 'price',
        title: '金额',
        align: 'center'
      }, {
        key: 'start_index',
        dataIndex: 'start_index',
        title: '起始章节',
        align: 'center'
      },{
        key: 'end_index',
        dataIndex: 'end_index',
        title: '结束章节',
        align: 'center'
      },{
        key: 'action',
        title: 'Action',
        align: 'center',
        render(item, record, index) {
          return (
            <Popconfirm title="确定删除这条内容吗?" onConfirm={() => that.handleDeleteItem(record, index)} okText="Yes" cancelText="No">
              <Button size='small' type='danger'>Delete</Button>
            </Popconfirm>
          )
        }
      }
    ];
    return (
      <Fragment>
        <span style={{float: 'right', marginRight: '30px'}}>共 {this.state.chapterCount} 章</span>
        <Button style={{margin: '0 30px 30px 0'}} type='primary'>
          <Link to={'/build/novel/config/create?id=' + this.state.book.id + '&title=' + this.state.book.title}>创建规则</Link>
        </Button>
        <Button onClick={() => this.props.history.goBack()}>返回上一级</Button>
        <Table bordered rowKey={record => record._id} columns={columns} dataSource={rules} />
      </Fragment>
    )
  }
  componentDidMount() {
    axios.get('http://h.tyuss.com/rule', {params: {id: this.state.book.id}})
      .then(res => {
        if(res.data.success === '成功') {
          let rules = res.data.data;
          let chapterCount = res.data.chapter_count;
          this.setState(() => ({rules, chapterCount}))
        }
      })
  }
  handleDeleteItem(record, index) {
    axios.get('http://h.tyuss.com/rule/delete', {params: {id: record._id}})
      .then(res => {
        if(res.data.success === '成功') {
          let rules = this.state.rules
          rules.splice(index, 1)
          this.setState(() => ({
            rules: [...rules]
          }))
          message.success('修改成功');
        } else {
          message.error('删除失败');
        }
      })
  }

}

export default Config;