import React, { PureComponent } from 'react';
import {Form, Input, Upload, Button, Icon, message} from 'antd';
import axios from 'axios';

class Modify extends PureComponent {
  constructor(props) {
    super(props);
    let record = decodeURI(props.location.search);
    record = JSON.parse(record.split('=')[1]);
    this.state = record;
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
  }
  render() {
    console.log(this.state)
    const { page_title, title, image_url } = this.state;
    const FormLabelCol = {
      labelCol: {
        sm: {span: 3},
      },
      wrapperCol: {
        sm: {span: 8},
      }
    };
    const props = {
      action: 'http://h.tyuss.com/book/upload',
      onChange: this.handleChange,
      showUploadList: false,
      name: 'imageFile'
    };
    const bannerStyle = {
      width: '200px'
    };
    return (
      <div className='modify'>
        <Form {...FormLabelCol}>
          <Form.Item label='小说名称'>
            <h3>{title}</h3>
          </Form.Item>
          <Form.Item label='页面标题'>
            <Input value={page_title} placeholder='请输入页面标题' onChange={this.handleInputChange}/>
          </Form.Item>
          <Form.Item label='banner图'>
            <Upload {...props}>
              {image_url === ''
                ? (<Button><Icon type="upload" />上传图片</Button>)
                : (<img style={bannerStyle} src={image_url} alt=""/>)}
            </Upload>
          </Form.Item>
          <Form.Item style={{marginLeft: '100px'}}>
            <Button style={{marginRight: '20px'}} type='primary' onClick={this.handleSubmit}>提交</Button>
            <Button type='danger' onClick={this.handleCancel}>取消</Button>
          </Form.Item>
        </Form>
      </div>
    )
  }
  handleInputChange(e) {
    let page_title = e.target.value;
    this.setState(() => ({page_title}))
  }
  handleChange(res) {
    if(res.file.status === 'done') {
      let image_url = 'http://h.tyuss.com/uploads/' + res.file.response.image_url
      this.setState(() => ({image_url}))
    }
  }
  handleSubmit() {
    axios.post('http://h.tyuss.com/book/update', {
      id: this.state._id,
      page_title: this.state.page_title,
      image_url: this.state.image_url
    }).then(res => {
      if(res.data.success === '成功') {
        message.success('修改成功');
        this.props.history.goBack()
      }
    })
  }
  handleCancel() {
    this.props.history.goBack();
  }
}

export default Modify;