import React, { PureComponent } from 'react';
import { Form, Input, Button, message } from 'antd';
import axios from 'axios';

class CreateRule extends PureComponent {
  constructor(props) {
    super(props);
    let record = decodeURI(props.location.search);
    let id = record.split('=')[1].split('&')[0];
    let title = record.split('=')[2]
    this.state = {
      book: {id, title},
      price: null,
      start: null,
      end: null
    };
    this.handlePriceChange = this.handlePriceChange.bind(this);
    this.handleStartChange = this.handleStartChange.bind(this);
    this.handleEndChange = this.handleEndChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCancel = this.handleCancel.bind(this);
  }
  render() {
    const FormLabelCol = {
      labelCol: {
        sm: {span: 3},
      },
      wrapperCol: {
        sm: {span: 8},
      }
    };
    return (
      <Form {...FormLabelCol}>
        <Form.Item label='小说名称'>
          <h3>{this.state.book.title}</h3>
        </Form.Item>
        <Form.Item label='付费金额'>
          <Input value={this.state.price} placeholder='请输入付费金额' onChange={this.handlePriceChange} />
        </Form.Item>
        <Form.Item label='起始章节'>
          <Input value={this.state.start} placeholder='请输入起始章节' onChange={this.handleStartChange} />
        </Form.Item>
        <Form.Item label='结束章节'>
          <Input value={this.state.end} placeholder='请输入结束章节' onChange={this.handleEndChange} />
        </Form.Item>
        <Form.Item style={{textAlign: 'center'}}>
          <Button style={{marginRight: '20px'}} type='primary' onClick={this.handleSubmit}>提交</Button>
          <Button type='danger' onClick={this.handleCancel}>取消</Button>
        </Form.Item>
      </Form>
    )
  }
  handlePriceChange(e) {
    let price = e.target.value;
    this.setState(() => ({
      price
    }))
  }
  handleStartChange(e) {
    let start = e.target.value;
    this.setState(() => ({
      start
    }))
  }
  handleEndChange(e) {
    let end = e.target.value;
    this.setState(() => ({
      end
    }))
  }
  handleSubmit() {
    let data = this.state;
    axios.post('http://h.tyuss.com/rule/create', {
      bid: Number(data.book.id),
      start: Number(data.start),
      end: Number(data.end),
      price: Number(data.price)
    }).then(res => {
      if(res.data.success === '成功') {
        message.success('创建成功');
        this.props.history.goBack();
      }
    })
  }
  handleCancel() {
    this.props.history.goBack();
  }
}

export default CreateRule;